import { Injectable } from '@angular/core';
import { ServiceStatus } from '../models/service-status.enum';
import { Observable } from 'rxjs';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Attachment } from '../models/attachment';

@Injectable({
  providedIn: 'root'
})
export class AttachmentsService {

  constructor(private http: HttpClient) { }
  baseUrl = '/api/attachments';

  asyncGetAttachments(token: string, kind: string): Observable<Array<Attachment>> {
    let session = window.localStorage.getItem('_session');

    if (! session) {
      session = 'loggedout';
    }
    return this.http.get<Attachment[]>(`${this.baseUrl}/${kind}/${token}`, {
      headers : {
        's-t': session
      }
    });
  }

  asyncPostAttachment(attachment: FormData, token: string, kind: string): Observable<ServiceStatus> {
    return new Observable<ServiceStatus>((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      this.http.post(`${this.baseUrl}/${kind}/${token}`, attachment, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((data: any) => {
        if (data.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.error(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      });
    });
  }

  asyncPostCopyAttachment(a: Attachment, destKind: string, destTkn: string): Observable<Attachment> {
    let session = window.localStorage.getItem('_session');
    if (! session) {
      session = 'loggedout';
    }
    return this.http.post<Attachment>(
      `${this.baseUrl}/copy/${a.kind}/${a.token}/${a._id}/to/${destKind}/${destTkn}`,
      {},
      {
        headers: {
          's-t': session
        }
      });
  }

  asyncPutAttachment(attachment: Attachment): Observable<ServiceStatus> {
    return new Observable<ServiceStatus>((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      const token = attachment.token;
      const kind = attachment.kind;
      this.http.put(`${this.baseUrl}/${kind}/${token}/${attachment._id}`, attachment, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((data: any) => {
        if (data.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.error(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      });
    });
  }

  asyncDeleteAttachment(attachment: Attachment): Observable<ServiceStatus> {
    return new Observable<ServiceStatus>((observer) => {
      let session = window.localStorage.getItem('_session');
      if (! session) {
        session = 'loggedout';
      }
      const token = attachment.token;
      const kind = attachment.kind;
      this.http.delete(`${this.baseUrl}/${kind}/${token}/${attachment._id}`, {
        headers : {
          's-t': session
        },
        observe: 'response',
        responseType: 'text'
      }).subscribe((data: any) => {
        if (data.status === 200) {
          observer.next(ServiceStatus.OK);
        } else {
          observer.error(ServiceStatus.FAILED);
        }
        return {unsubscribe() {}};
      });
    });
  }
}
